const rules = {
  tradeWay: [{
    required: true,
    message: '',
    trigger: 'change'
  }],
  closepositionTime: [{
    type: 'date',
    required: true,
    message: '',
    trigger: 'change'
  }],
  closepositionPrice: [{
    required: true,
    message: '',
    trigger: 'blur'
  }],
  closepositionOptionPrice: [{
    required: true,
    message: '',
    trigger: 'blur'
  }],
  username: [{
    required: true,
    message: "",
    trigger: "blur"
  }],
  password: [{
    required: true,
    message: "",
    trigger: "blur"
  }],
  newPwdConfirm: [{
    validator: validatePass,
    trigger: 'blur'
  }],
  customerName: [{
    required: true,
    message: "",
    trigger: "blur"
  }],
  customerNo: [{
    required: true,
    message: "",
    trigger: "blur" 
  }],
  originalPrice: [{
    required: true,
    message: "",
    trigger: "blur" 
  }],
  contractNo: [{
    required: true,
    message: "",
    trigger: "blur" 
  }],
  totalLoan: [{
    required: true,
    message: "",
    trigger: "blur" 
  }],
  tradeEndtime: [{
    required: true,
    message: "",
    trigger: "blur" 
  }],
  customerPhone: [{
    required: true,
    message: "",
    trigger: "blur"
  }],
  recoveryRate: [{
    required: true,
    message: "",
    trigger: "blur"
  }],
  goodsCode: [{
    required: true,
    message: "",
    trigger: "blur"
  }],
}

function validatePass(rule, value, callback) {
  if (value === '' || !value) {
    callback(new Error(''));
  } else if (value !== this.modifyPwdData.newPwd) {
    callback(new Error('!'));
  } else {
    callback();
  }
};

export {
  rules
}



